body .client{
    align-content: center;
    align-items: center;
    border: none;
    bottom: 0;
    display: flex;
    flex-direction: column;
    height: 87%;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    right: 0;
    width: 85%;
    z-index: 100;
}

::placeholder {
    color: #999;
    opacity: .6; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #999;
    opacity: .6;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: #999;
    opacity: .6;
}

.container-loading-client {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
}

.container-loading-client .loading-client { color: #0191CE; }

/* Modal */
.ReactModalPortal > div{ opacity: 0; }

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
}

.ReactModalPortal .ReactModal__Overlay--after-open { opacity: 1; }

.ReactModalPortal .ReactModal__Overlay--before-close { opacity: 0; }

.modal {
    background: #f7f7f7;
    border-radius: .7rem;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 620px;
    max-width: 500px;
    outline: none;
    padding: 10px 1.5rem;
}

.form-client-modal header {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: .3rem;
}

.form-client-modal header p { margin: 0; }

.title-client-modal { margin: 0; }

.required-fields-modal {
    color: #0191CE;
    font-size: 10px;
    font-weight: 700;
}

hr{ 
    background-color: #ccc;
    border: none; 
    height: 1px;
    margin-bottom: 1rem;
}

.form-client-modal i {
    color: #0191CE;
    font-size: 15px;
    font-weight: 800;
}

.form-client-modal input {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    height: 1.7rem;
    margin-top: .5rem;
    width: 17rem;
}

.form-client-modal label, .form-client-modal input, .form-client-modal p {
    font-size: .9rem;
    margin: 0;
    margin-bottom: .5rem;
}

.form-client-modal input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.form-client-modal input:hover { border: 1px solid rgb(3, 139, 198); }

.codigo-input-modal {
    display: flex;
    flex-direction: row;
}

.codigo-input-modal p { padding-left: .5rem; }

.codigo-input-modal label, .codigo-input-modal p { font-size: 1rem !important; }

.ruc-input-modal, .nombre-input-modal, .direccion-input-modal, 
.telefono-input-modal, .correo-input-modal {
    display: flex;
    flex-direction: column;
}

.ruc-input-modal input, .nombre-input-modal input, .direccion-input-modal input, 
.telefono-input-modal input, .correo-input-modal input { width: 25rem; }

.iva-input-modal {
    display: flex;
    flex-direction: row;
    margin-bottom: .5rem; 
}

.buttons-column {
    display: flex;
    justify-content: space-evenly;
}

.button-save-modal, .button-close-modal {
    background: none;
    border-radius: .4rem;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding: .5rem 1rem;
}

.button-save-modal {
    border: 2px solid #0191CE;
    border-color: #0191CE;
    color: #0191CE;
}

.button-save-modal:hover {
    background: #0191CE;
    color: #FFFFFF;
    cursor: pointer;
}

.button-close-modal {
    border: 2px solid #ef020a;
    border-color: #ef020a;
    color: #ef020a;
}

.button-close-modal:hover {
    background: #ef020a;
    color: #FFFFFF;
    cursor: pointer;
}

.container-client-details input, .container-client-details label{ display: block; }

label {
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
}

.container-client-details i {
    color: #0191CE;
    font-size: 15px;
    font-weight: 800;
}

.container-client-details input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.container-client-details input:hover { border: 1px solid rgb(3, 139, 198); }

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    margin: 0; 
    -webkit-appearance: none; 
}

input[type=number] { -moz-appearance:textfield; }

.container-client-header { width: 95%; }

.container-client-header h1 {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    margin-bottom: .3rem;
    margin-top: 0;
}

.container-client-details {
    border: 1px solid #D2D2D2;
    border-radius: .5rem;
    padding: .5rem;
}

.first-column-client-input, .second-column-client-input, .third-column-client-input {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: .5rem;
    width: 100%;
}

.third-column-client-input { justify-content: flex-start; }

.first-column-client-input input, .second-column-client-input input, .third-column-client-input input {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    height: 1.4rem;
    margin-top: .5rem;
}

.codigo-input input, .ruc-input input { width: 10rem; }

.nombre-input input { width: 38rem; }

.direccion-input input { width: 42rem; }

.telefono-input input { width: 20rem; }

.iva-input {
    display: flex;
    justify-content: center;
    margin-left: 6rem;
}

input[type=checkbox] {
    appearance: none;
    cursor: pointer;
    height: 1rem;
    margin: 0;
    margin-left: .5rem;
    position: relative;
    width: 1rem;
    -moz-appearance: none;
    -webkit-appearance: none;
}

input[type=checkbox]:before {
    content: "";
    display: block;
    height: 1rem;
    left: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0;
    width: 1rem;
}

input[type=checkbox]:checked:before {
    background-color:#0191CE;
    border: .1px solid #0191CE;
    border-radius: .1rem;
    content: "";
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

input[type=checkbox]:checked:after {
    border: solid white;
    border-width: 0 2px 2px 0;
    content: "";
    display: block;
    height: .4rem;
    left: 6px;
    position: absolute;
    top: 2px;
    transform: rotate(45deg);
    width: .1rem;
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}

.correo-input input { width: 30rem; }

.button-save-column {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
}

.button-save {
    background: #0191CE;
    border: none;
    border-radius: 5rem;
    box-shadow: 3px 4px 4px rgba(1, 145, 206, 0.5);
    color: #FFFFFF;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 700;
    height: 3rem;
    width: 15rem;
}

.button-save:hover {
    background: #15749d;
    box-shadow: 3px 4px 4px rgba(0, 64, 92, 0.5);
    color: #d8d8d8;
    cursor: pointer;
}

.button-save-block {
    background: #003c55;
    border: none;
    border-radius: 5rem;
    box-shadow: 3px 4px 4px rgba(1, 145, 206, 0.5);
    color: #FFFFFF;
    cursor: progress;
    font-size: 1.3rem;
    font-weight: 700;
    height: 4rem;
    text-align: center;
    width: 100%;
}

.required-fields {
    color: #0191CE;
    font-size: 12px;
    font-weight: 700;
    margin: 0;
    position: absolute;
    right: 1.3rem;
    top: 0;
}

.table-client {
    border: 1px solid #D2D2D2;
    border-radius: .5rem;
    margin-top: .5rem;
    overflow: hidden;
    width: 100%;
}

.table-client table {
    border-collapse: collapse;
    width: 100%;
}

.table-client table thead {
    background: rgba(1, 145, 206, 0.25);
    color: #0977A6;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
}

.table-client table thead th {
    padding: .5rem;
    text-align: center;
}

.table-client table tbody {
    background: #FFFFFF;
    font-size: .8rem;
    font-weight: 100;
}

.table-client table tbody tr:nth-child(even) { background-color: #f4f4f4; } 

.table-client table tbody tr td {
    padding: .5rem;
    text-align: center;
}

.table-client table tbody tr td:nth-child(n+3) { padding: .1rem .5rem; }

.table-client table tbody tr td .container-actions {
    align-items: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
}

.table-client table tbody tr td .container-actions-1,
.table-client table tbody tr td .container-actions-2,
.table-client table tbody tr td .container-actions-3 {
    align-items: center;
    border-radius: 5rem;
    display: flex;
    justify-content: center;
    padding: .3rem 1rem; 
}

.table-client table tbody tr td .container-actions-1 { background: rgba(1, 145, 206, 0.85); }

.table-client table tbody tr td .container-actions-2 { background: rgba(206, 26, 1, 0.85); }

.table-client table tbody tr td .container-actions-3 {
    background: rgba(54, 206, 1, 0.85);
    padding: .2rem .8rem;
}

.pagination {
    display: flex;
    justify-content: center;
    margin-bottom: .5rem;
    margin-top: .5rem;
    width: 100%;
}

.pagination-1 {
    align-items: center;
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: 5rem;
    display: flex;
    height: 2rem;
    justify-content: space-between;
    overflow: hidden;
    width: 10rem;
}

.pagination-button {
    background: none;
    border-bottom: none;
    border-left: none;
    border-right: 1px solid #D2D2D2;
    border-top: none;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 700;
    height: 2rem;
    width: 2rem;
}

.pagination-button:last-child {
    border-bottom: none;
    border-left: 1px solid #D2D2D2;
    border-right: none;
    border-top: none;
}

.pagination-1 .pagination-num { display: flex; }

.pagination-1 .current-page {
    color: #5a5a5a;
    font-size: 1.1rem;
    margin-right: .3rem;
}

.pagination-1 .total-pages {
    font-size: 1.1rem;
    font-weight: 900;
}

.pagination-button:hover {
    background: #006a97;
    color: #FFFFFF;
}

@media only screen and (max-width: 1300px) {
    
    .nombre-input input { width: 30rem; }

    .direccion-input input { width: 35rem; }
    
}

@media only screen and (max-width: 1140px) {
    
    .codigo-input input, .ruc-input input { width: 13rem; }
    
    .nombre-input input { width: 23rem; }

    .direccion-input input { width: 30rem; }
    
}

@media only screen and (max-width: 1040px) {
        
    .nombre-input input { width: 20rem; }
    
    .direccion-input input { width: 27rem; }
        
}

@media only screen and (max-width: 960px) {
    /* Ocultar SideBar */
    body .client {
        width: 100%;
        z-index: 0;
    }

    .nombre-input input { width: 25rem; }

    .direccion-input input { width: 30rem; }
    
}

@media only screen and (max-width: 900px) {

    .codigo-input input, .ruc-input input { width: 10rem; }
        
    .nombre-input input { width: 20rem; }
    
    .correo-input input, .direccion-input input { width: 25rem; }
        
}

@media only screen and (max-width: 485px) {
    .modal { width: 290px; }

    .ruc-input-modal input, .nombre-input-modal input, .direccion-input-modal input, 
    .telefono-input-modal input, .correo-input-modal input { width: 100%; }

}

@media only screen and (max-width: 795px) {
    
    .direccion-input input, .correo-input input { width: 20rem; }
        
}

@media only screen and (max-width: 700px) {
        
    .nombre-input input, .telefono-input input { width: 15rem; }
        
}

@media only screen and (max-width: 620px) {
    
    .codigo-input input, .ruc-input input { width: 7rem; }

    .direccion-input input { width: 15rem; }

    .nombre-input input, .telefono-input input { width: 12rem; }

    .correo-input input { width: 18rem; }

    .iva-input { margin-left: 2.5rem; }

    .table-client {
        border: none;
        margin-top: 1.5rem;
    }

    .table-client table thead { display: none; }

    .table-client table, .table-client table tbody, .table-client table tr, .table-client table td { display: block; }

    .table-client table tr { margin-bottom: .5rem; }

    .table-client table tbody tr td {
        border-bottom: 1px solid #D2D2D2;
        padding: .5rem;
        padding-right: 1rem;
        text-align: right;
    }

    .table-client table tbody tr td:nth-child(n+3) {
        padding: .5rem;
        padding-right: 1rem;
        text-align: right;
    }

    .table-client table td {
        position: relative;
        text-align: right;
    }

    .table-client table td::before {
        content: attr(data-label);
        font-size: 1rem;
        font-weight: 700;
        left: 0;
        padding-left: 1rem;
        position: absolute;
        text-align: left;
        width: 50%;
    }

    .table-client table tbody tr td .container-actions { justify-content: end; }

}

@media only screen and (max-width: 480px) {

    .container-client-details input { height: 2rem; border-radius: .5rem; }

    .iva-input input { height: 1rem; border-radius: .2rem; }

    .container-client-details input, .container-client-details label{
        display: inline-block;
        margin-bottom: 1rem;
    }

    .container-client-details label{
        text-align: end;
        width: 81.391px;
    }

    .first-column-client-input, .second-column-client-input, .third-column-client-input { 
        align-items: flex-start;
        flex-direction: column; 
        margin: 0;
        padding-left: .5rem;
    }

    .codigo-input, .ruc-input, .nombre-input, .direccion-input, .telefono-input, .correo-input  { width: 100%; }

    .codigo-input input, .ruc-input input, .nombre-input input, .direccion-input input, .telefono-input input, .correo-input input { margin-left: 1rem; width: 70%; }

    .iva-input input { margin-left: 1rem; }

    .iva-input { margin: 0; }

}

@media only screen and (max-width: 380px) {
    .first-column-client-input, .second-column-client-input, .third-column-client-input { padding-left: 0rem; }
}

@media only screen and (max-width: 360px) {
    
    .container-client-details label{
        text-align: end;
        width: 75px;
    }

    .iva-input label { font-size: .8rem; }

}

@media only screen and (max-width: 350px) {
    
    .container-client-header { min-width: 332.5px; }

}

@media only screen and (max-height: 500px) {

    body .client { height: 80%; }

    .ReactModalPortal .ReactModal__Overlay {
        align-items: flex-start;
        justify-content: center;
    }

    .modal { margin: 1rem 0; }

}